:root {
  /* Fonts */
  --font-primary: "Poppins", sans-serif;
  /* Colors */
  --primary-color: #3caab4;
  --secondary-color: #0074A2;
  --tertiary-color: #F08C3C;
  --gray-color: #8E8E8E;
  --body-color: #1B1B1B;
  --white-color: #FFFFFF;
  --black-color: #000000;
  /* Font Sizes */
  --font-size-h1: 20px;
  --font-size-h2: 18px;
  --font-size-body: 16px;
  --font-size-14: 14px;
  /* Font Weights */
  --font-weight-700: 700;
  --font-weight-600: 600;
  --font-weight-500: 500;
  --font-weight-400: 400;
  /* Border Radius */
  --border-radius-large: 8px;
  --border-radius: 4px;
}

.zwssgr-nav-tab-wrapper {
  border-bottom: 1px solid rgb(221, 225, 231);
}
.zwssgr-nav-tab-wrapper .nav-tab {
  background: none;
  box-shadow: none;
  border: none;
  color: #285064;
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-body);
  font-family: var(--font-primary);
  padding-bottom: 15px;
}
.zwssgr-nav-tab-wrapper .nav-tab-active {
  border-bottom: 3px solid #11AAB4;
}
.zwssgr-tab-item {
  font-size: var(--font-size-14);
  font-weight: var(--font-weight-400);
  color: #4A494B;
  font-family: var(--font-primary);
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  pointer-events: none;
  text-transform: capitalize;
  padding-bottom: 10px;
}
.zwssgr-tab-item .zwssgr-step {
  font-weight: var(--font-weight-600);
  color: var(--body-color);
  margin-right: 10px;
  position: relative;
  width: 18px;
  height: 19px;
  background: #f0f0f1;
  border-radius: 200%;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
}
.zwssgr-tab-item .zwssgr-step.done {
  font-size: 0;
}
.zwssgr-tab-item .zwssgr-step.done:before {
  content: url("../../images/step-done-icon.png");
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 18px;
  width: 18px;
  margin: 0;
}
.zwssgr-tab-item.done {
  pointer-events: all;
}
.zwssgr-tab-item.done .zwssgr-step {
  font-size: 0;
}
.zwssgr-tab-item.done .zwssgr-step:before {
  content: url("../../images/step-done-icon.png");
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 18px;
  width: 18px;
  margin: 0;
}
.zwssgr-tab-item.active {
  color: #285064;
  font-weight: var(--font-weight-600);
  pointer-events: all;
}
.zwssgr-custom-tab {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 30px 0;
}
.zwssgr-custom-tab .zwssgr-step-arrow {
  position: relative;
  margin: 0px 20px;
}
.zwssgr-custom-tab .zwssgr-step-arrow:before, .zwssgr-custom-tab .zwssgr-step-arrow:after {
  content: "";
  background: #4A494B;
  position: relative;
  width: 10px;
  height: 2px;
  display: block;
  transform: rotate(45deg);
  border-radius: 2px;
  top: 6px;
}
.zwssgr-custom-tab .zwssgr-step-arrow:after {
  transform: rotate(-45deg);
  top: 10px;
}
.zwssgr-tab-content-display {
  display: none;
}
.zwssgr-google-tab-text {
  font-size: var(--font-size-body);
  font-weight: var(--font-weight-500);
  font-family: var(--font-primary);
  line-height: 1.6;
  color: red;
}
.zwssgr-tab-content {
  display: none;
  padding-right: 1rem;
}
.zwssgr-tab-content.active {
  display: block;
}

.swiper-button-next.zwssgr-swiper-button-next:after,
.swiper-button-prev.zwssgr-swiper-button-prev:after {
  font-size: 30px;
  font-weight: 900;
  color: #3A4554;
}

.swiper-button-next.zwssgr-swiper-button-next {
  right: 0;
}

.swiper-button-prev.zwssgr-swiper-button-prev {
  left: 0;
}

.zwssgr-slider {
  max-width: 100%;
  height: fit-content;
  position: relative;
  padding: 0 30px;
}
.zwssgr-slider .zwssgr-list-inner {
  background-color: #FAFAFA;
  padding: 20px;
  border-radius: var(--border-radius-large);
  height: 100%;
}
.zwssgr-slider .zwssgr-rating {
  width: 90px;
}
.zwssgr-slider .zwssgr-star {
  color: #ccc;
  font-size: 20px;
}
.zwssgr-slider .zwssgr-star.filled {
  color: var(--tertiary-color);
}
.zwssgr-slider.zwssgr-slider4 {
  max-width: 650px;
  margin: 0 auto;
  padding: 0 30px;
}
.zwssgr-slider.zwssgr-slider5 {
  max-width: 750px;
  margin: 0 auto;
  text-align: center;
}
.zwssgr-slider.zwssgr-slider5 .zwssgr-slide-item {
  background-color: transparent;
  padding: 0;
}
.zwssgr-slider.zwssgr-slider5 .zwssgr-contnt-wrap {
  background-color: #FAFAFA;
  padding: 20px;
  border-radius: var(--border-radius-large);
}
.zwssgr-slider.zwssgr-slider5 .zwssgr-rating {
  margin: 0 auto 30px;
}
.zwssgr-slider.zwssgr-slider5 .zwssgr-title {
  margin-top: 20px;
}
.zwssgr-slider.zwssgr-slider6 .zwssgr-content {
  text-align: center;
}
.zwssgr-slider.zwssgr-slider6 .zwssgr-rating {
  margin: 0 auto 10px;
}
.zwssgr-slider .zwssgr-slide-item {
  background: #FAFAFA;
  border-radius: var(--border-radius-large);
  height: auto;
}
.zwssgr-slider .zwssgr-slide-item:focus {
  outline: none;
}
.zwssgr-slider .zwssgr-slide-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 10px;
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-review-info {
  width: calc(100% - 120px);
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-profile {
  width: 62px;
  position: relative;
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-profile img {
  width: 62px;
  height: 62px;
  border-radius: 100%;
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-google-icon {
  width: 20px;
  height: 20px;
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-google-icon img {
  width: 20px;
  height: 20px;
}
.zwssgr-slider .zwssgr-content {
  font-size: var(--font-size-14);
  color: var(--body-color);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-400);
  margin: 10px 0;
}
.zwssgr-slider .zwssgr-title {
  font-size: var(--font-size-body);
  color: var(--body-color);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-700);
  margin: 0 0 5px 0;
}
.zwssgr-slider .zwssgr-days-ago {
  font-size: var(--font-size-14);
  color: var(--gray-color);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-400);
  margin: 0;
  line-height: normal;
}
.zwssgr-slider .slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 9;
  padding: 0;
  width: 18px;
  height: 18px;
  color: #3A4554;
  border: 0;
  border-radius: 100%;
  background-color: transparent;
  transform: translate(0, -50%);
  transition: border-color ease 0.3s;
  font-size: 0;
  cursor: pointer;
}
.zwssgr-slider .slick-arrow.slick-prev {
  left: -3%;
  transform: rotate(-45deg);
  transform-origin: center center;
}
.zwssgr-slider .slick-arrow.slick-prev::before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  border-left: 4px solid #3A4554;
  border-top: 4px solid #3A4554;
  box-sizing: border-box;
}
.zwssgr-slider .slick-arrow.slick-next {
  right: -3%;
  left: auto;
  transform: rotate(45deg);
  transform-origin: center center;
}
.zwssgr-slider .slick-arrow.slick-next::before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  border-right: 4px solid #3A4554;
  border-top: 4px solid #3A4554;
  box-sizing: border-box;
}
.zwssgr-slider .slick-track {
  display: flex;
}
.zwssgr-slider .slick-track .zwssgr-slide-item {
  height: auto;
}
.zwssgr-slider .slick-track .zwssgr-list-inner {
  height: 100%;
  background-color: var(--white-color);
}

.zwssgr-slider3 {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding: 0;
}
.zwssgr-slider3 .zwssgr-slider-badge {
  width: 240px;
  text-align: center;
}
.zwssgr-slider3 .zwssgr-slider-badge .zwssgr-rating {
  margin: 0 auto;
}
.zwssgr-slider3 .zwssgr-slider-3-wrap {
  width: calc(100% - 300px);
  position: relative;
  padding: 0 30px;
}

.zwssgr-list {
  padding: 0;
}
.zwssgr-list .zwssgr-slide-item {
  padding: 0;
  height: auto;
  margin-bottom: 20px;
}
.zwssgr-list .zwssgr-list-inner {
  background-color: #FAFAFA;
  padding: 20px;
  border-radius: var(--border-radius-large);
  height: 100%;
}

.zwssgr-list2 .zwssgr-slide-item .zwssgr-list-inner {
  background-color: var(--white-color);
  border: 1px solid #E8EBEF;
}
.zwssgr-list2 .zwssgr-list-content-wrap {
  padding-left: 80px;
}

.zwssgr-list3 .zwssgr-slide-item {
  text-align: center;
}
.zwssgr-list3 .zwssgr-rating {
  margin: 0 auto;
}

.zwssgr-list4 .zwssgr-slide-item,
.zwssgr-list5 .zwssgr-slide-item {
  background-color: var(--white-color);
  border-bottom: 2px solid #E8EBEF;
  border-radius: 0;
  margin-bottom: 0;
}

.zwssgr-list-wrap3 {
  max-width: 250px;
  margin: 20px auto 0;
}
.zwssgr-list-wrap3 .zwssgr-review-info {
  width: calc(100% - 70px);
  text-align: left;
}

.zwssgr-list-wrap5 {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}
.zwssgr-list-wrap5 .zwssgr-prifile-wrap {
  width: 30%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.zwssgr-list-wrap5 .zwssgr-prifile-wrap .zwssgr-profile {
  width: 62px;
  height: 62px;
}
.zwssgr-list-wrap5 .zwssgr-prifile-wrap .zwssgr-data {
  width: calc(100% - 80px);
}
.zwssgr-list-wrap5 .zwssgr-content-wrap {
  width: 70%;
  padding-left: 10px;
  box-sizing: border-box;
}
.zwssgr-list-wrap5 .zwssgr-content-wrap .zwssgr-review-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 10px;
}